<template>
  <div class="halo-background">
    <div v-for="star in stars" :key="star.id" :style="star.style" class="halo-star"></div>
    <div class="halo-glow halo-glow1"></div>
    <div class="halo-glow halo-glow2"></div>
    <div class="halo-glow halo-glow3"></div>
  </div>
</template>

<script setup>
import { ref, onMounted } from 'vue';

const stars = ref([]);
const NUM_STARS = 100;

const createStar = () => {
  const size = Math.random() * 1.2 + 0.4;
  const x = Math.random() * 100;
  const y = Math.random() * 100;
  const opacity = Math.random() * 0.5 + 0.3;
  const blur = Math.random() * 2 + 1;
  return {
    id: Date.now() + Math.random(),
    style: {
      width: `${size}px`,
      height: `${size}px`,
      left: `${x}vw`,
      top: `${y}vh`,
      opacity,
      filter: `blur(${blur}px)`,
    },
  };
};

onMounted(() => {
  for (let i = 0; i < NUM_STARS; i++) {
    stars.value.push(createStar());
  }
});
</script>

<style scoped>
.halo-background {
  position: fixed;
  inset: 0;
  width: 100vw;
  height: 100vh;
  overflow: hidden;
  z-index: -1;
  background: linear-gradient(180deg, #0a1330 0%, #1a1b3a 100%);
}

/* 鏌斿拰鏄熺偣 */
.halo-star {
  position: absolute;
  background: radial-gradient(circle, #e0eaffcc 60%, #6a7cff00 100%);
  border-radius: 50%;
  pointer-events: none;
  transition: opacity 0.8s;
}

/* 澶氬眰鍏夋檿 */
.halo-glow {
  position: absolute;
  border-radius: 50%;
  pointer-events: none;
  z-index: 1;
  mix-blend-mode: lighten;
  opacity: 0.38;
  animation: halo-pulse 8s ease-in-out infinite alternate;
}
.halo-glow1 {
  width: 600px;
  height: 600px;
  left: 10vw;
  top: 20vh;
  background: radial-gradient(circle, #6a7cff88 0%, #a66aff22 60%, transparent 100%);
  animation-delay: 0s;
}
.halo-glow2 {
  width: 420px;
  height: 420px;
  right: 8vw;
  top: 50vh;
  background: radial-gradient(circle, #00eaff55 0%, #6a7cff22 60%, transparent 100%);
  animation-delay: 2s;
}
.halo-glow3 {
  width: 320px;
  height: 320px;
  left: 50vw;
  top: 70vh;
  background: radial-gradient(circle, #a66aff55 0%, #00eaff22 60%, transparent 100%);
  animation-delay: 4s;
}

@keyframes halo-pulse {
  0%   { opacity: 0.18; filter: blur(2px);}
  30%  { opacity: 0.65; filter: blur(16px);}
  50%  { opacity: 0.85; filter: blur(24px);}
  70%  { opacity: 0.65; filter: blur(16px);}
  100% { opacity: 0.18; filter: blur(2px);}
}

/* 绉诲姩绔�潤姝� */
@media (max-width: 800px) {
  .halo-star, .halo-glow {
    animation: none !important;
    opacity: 0.28;
  }
}
</style>

