<template>
  <footer class="foot-menu">
    <div class="footer-content">
      <!-- 左侧内容：版权+驱动 -->
      <div class="left-info">
        <p class="copyright">© {{ currentYear }} {{ siteConfig.siteName || '云阶快传' }} | 版权所有</p>
        <div class="powered-by">由 YKC 驱动 | StarUI V3</div>
      </div>
      <!-- 右侧内容：备案号 -->
      <div class="right-info">
        <div class="record-info">
          <a v-if="siteConfig.icp" href="https://beian.miit.gov.cn" target="_blank">{{ siteConfig.icp }}</a>
          <span v-if="siteConfig.icp && siteConfig.securityRecord">&nbsp;</span>
          <a v-if="siteConfig.securityRecord" href="https://beian.mps.gov.cn/#/query/webSearch" target="_blank" class="security-record">
            <img src="/img/beian.png" alt="网安备案" class="beian-icon">
            {{ siteConfig.securityRecord }}
          </a>
        </div>
      </div>
    </div>
  </footer>
</template>

<script>
export default {
  name: 'FootMenu',
  data() {
    return {
      siteConfig: {
        siteName: '云阶快传',
        icp: '',
        securityRecord: ''
      },
      currentYear: new Date().getFullYear()
    }
  },
  async mounted() {
    await this.loadSiteConfig()
  },
  methods: {
    async loadSiteConfig() {
      // 全局缓存变量 window._siteConfigCache
      if (window._siteConfigCache) {
        this.siteConfig = window._siteConfigCache
        return
      }
      try {
        const protocol = window.location.protocol
        const host = window.location.host
        const apiUrl = `${protocol}//${host}/api/data/site.json`
        const response = await fetch(apiUrl, {
          method: 'GET',
          headers: {
            'Content-Type': 'application/json'
          }
        })
        if (response.ok) {
          const config = await response.json()
          const siteConfig = {
            siteName: config.siteName || '云阶快传',
            icp: config.icp || '',
            securityRecord: config.securityRecord || ''
          }
          this.siteConfig = siteConfig
          window._siteConfigCache = siteConfig // 写入全局缓存
        } else {
          console.warn('无法加载站点配置，使用默认值')
        }
      } catch (error) {
        console.error('加载站点配置失败:', error)
      }
    }
  }
}
</script>

