<template>
  <div class="top-menu-container">
    <!-- 桌面端顶部菜单 -->
    <!-- 桌面端顶部菜单 -->
    <div class="desktop-menu" v-show="!isMobile">
      <div class="menu-content">
        <div class="menu-left">
          <div class="logo-container">
            <img src="/img/logo.png" alt="Logo" class="logo-image">
          </div>
          <div class="menu-items">
            <div 
              v-for="(item, index) in menuItems" 
              :key="index"
              class="menu-item"
              :class="{ 'active': activeMenu === item.path }"
              @click="navigateTo(item.path)"
            >
              <i :class="item.icon"></i>
              <span>{{ item.name }}</span>
            </div>
          </div>
        </div>
        <div class="menu-right">
          <div class="user-info" @click="toggleDropdown" style="cursor:pointer;">
            
            <div class="user-details">
              <div class="user-name">{{ userInfo.nickname || '游客' }}</div>
              <div class="welcome-text">{{ welcomeText }}</div>
            </div>
            <!-- 自定义下拉菜单区域开始 -->
            <div class="custom-dropdown" ref="dropdownRef">
              <div class="user-avatar">
                <img :src="userInfo.avatar || '/img/default-avatar.png'" alt="Avatar">
              </div>
              <!-- 下拉菜单内容 -->
              <div class="dropdown-menu" :class="{ 'show': dropdownVisible }">
                <div class="dropdown-item" @click="navigateToSettings">
                  <i class="fas fa-user-cog"></i>
                  <span>账号设置</span>
                </div>
                <div class="dropdown-divider"></div>
                <div class="dropdown-item" @click="handleLogout">
                  <i class="fas fa-sign-out-alt"></i>
                  <span>退出登录</span>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
    
    <!-- 移动端顶部栏 -->
    <!-- 移动端顶部栏 -->
    <div class="mobile-top-bar" v-show="isMobile">
      <div class="menu-content">
        <div class="menu-toggle" @click="toggleSidebar">
          <i :class="sidebarOpen ? 'fas fa-times' : 'fas fa-bars'"></i>
        </div>
        <div class="mobile-user-info" @click="toggleDropdown" style="cursor:pointer;">
          <div class="user-name">{{ userInfo.nickname || '游客' }}</div>
          <div class="user-avatar">
            <img :src="userInfo.avatar || '/img/default-avatar.png'" alt="Avatar">
          </div>
          <!-- 移动端下拉菜单 -->
          <div class="dropdown-menu mobile-dropdown" :class="{ 'show': dropdownVisible }">
            <div class="dropdown-item" @click="navigateToSettings">
              <i class="fas fa-user-cog"></i>
              <span>账号设置</span>
            </div>
            <div class="dropdown-divider"></div>
            <div class="dropdown-item" @click="handleLogout">
              <i class="fas fa-sign-out-alt"></i>
              <span>退出登录</span>
            </div>
          </div>
        </div>
      </div>
    </div>
    
    <!-- 移动端侧边栏 -->
    <div class="mobile-sidebar" :class="{ 'open': sidebarOpen }" v-show="isMobile">
      <div class="sidebar-logo">
        <img src="/img/logo.png" alt="Logo" class="logo-image">
      </div>
      <div class="sidebar-menu">
        <div 
          v-for="(item, index) in menuItems" 
          :key="index"
          class="sidebar-menu-item"
          :class="{ 'active': activeMenu === item.path }"
          @click="navigateTo(item.path); closeSidebar()"
        >
          <i :class="item.icon"></i>
          <span>{{ item.name }}</span>
        </div>
      </div>
    </div>
    
    <!-- 遮罩层 -->
    <div class="sidebar-overlay" v-if="sidebarOpen" @click="closeSidebar"></div>
  </div>
</template>

<script setup>
import { ref, computed, onMounted, onUnmounted, inject } from 'vue';
import { useRouter, useRoute } from 'vue-router';
import { logout, clearVisitJwt, getCurrentUser } from '../utils/auth';
import { ElMessage } from 'element-plus';

const router = useRouter();
const route = useRoute();

// 注入全局confirm弹窗
const confirm = inject('confirm');

// 退出登录
const handleLogout = (event) => {
  if (event) {
    event.stopPropagation();
  }
  closeDropdown();
  
  confirm.open({
    title: '退出确认',
    message: '确定要退出登录吗？',
    confirmButtonText: '确定',
    cancelButtonText: '取消',
    onConfirm: async () => {
      try {
        // 调用后端退出接口
        const success = await logout();
        
        if (success) {
          ElMessage.success('已成功退出登录');
        }
        
        // 清除前端状态（只清理访问JWT，HttpOnly Cookie由后端处理）
        clearVisitJwt();
        
        // 跳转到登录页
        window.location.href = '/login';
        
      } catch (e) {
        console.error('退出登录请求失败:', e);
        // 即使网络错误，也清除本地状态并跳转到登录页
        clearVisitJwt();
        window.location.href = '/login';
      }
    }
  });
};

// 菜单项配置
const menuItems = ref([
  { name: '文件', path: '/file', icon: 'fas fa-folder' },
  { name: '分享', path: '/share', icon: 'fas fa-share-alt' },
  { name: '设置', path: '/settings', icon: 'fas fa-cog' },
]);

// 缓存的用户信息，避免频繁解析JWT
let cachedUserInfo = null;
let lastJwtCheck = 0;
const JWT_CACHE_DURATION = 5000; // 5秒缓存

// 用户信息（从访问JWT中获取用户名，添加缓存机制）
const userInfo = computed(() => {
  const now = Date.now();
  
  // 如果缓存有效且未过期，直接返回缓存
  if (cachedUserInfo && (now - lastJwtCheck) < JWT_CACHE_DURATION) {
    return cachedUserInfo;
  }
  
  // 重新获取用户信息
  const currentUser = getCurrentUser();
  cachedUserInfo = {
    nickname: currentUser?.username || '游客',
    avatar: '/img/default-avatar.png'
  };
  lastJwtCheck = now;
  
  return cachedUserInfo;
});

// 预加载图片函数
const preloadImages = () => {
  const imagesToPreload = [
    '/img/logo.png',
    '/img/default-avatar.png'
  ];
  
  imagesToPreload.forEach(src => {
    const img = new Image();
    img.src = src;
  });
};

// 欢迎语
const welcomeText = computed(() => {
  const hour = new Date().getHours();
  if (hour < 6) return '夜深了';
  if (hour < 9) return '早上好';
  if (hour < 12) return '上午好';
  if (hour < 14) return '中午好';
  if (hour < 18) return '下午好';
  if (hour < 22) return '晚上好';
  return '夜深了';
});

// 响应式状态
const isMobile = ref(false);
const sidebarOpen = ref(false);
const dropdownVisible = ref(false);
const dropdownRef = ref(null);
const activeMenu = computed(() => route.path);

// 切换下拉菜单
const toggleDropdown = (event) => {
  event.stopPropagation();
  dropdownVisible.value = !dropdownVisible.value;
};

// 关闭下拉菜单
const closeDropdown = () => {
  dropdownVisible.value = false;
};

// 处理点击外部关闭下拉菜单
const handleClickOutside = (event) => {
  if (dropdownRef.value && !dropdownRef.value.contains(event.target)) {
    // 检查是否点击了移动端用户信息区域
    const mobileUserInfo = document.querySelector('.mobile-user-info');
    if (mobileUserInfo && !mobileUserInfo.contains(event.target)) {
      closeDropdown();
    } else if (!mobileUserInfo) {
      closeDropdown();
    }
  }
};

// 检测窗口大小变化
const checkScreenSize = () => {
  isMobile.value = window.innerWidth < 768;
  if (!isMobile.value) {
    sidebarOpen.value = false;
  }
};

// 切换侧边栏
const toggleSidebar = () => {
  sidebarOpen.value = !sidebarOpen.value;
  if (sidebarOpen.value) {
    document.body.style.overflow = 'hidden'; // 防止背景滚动
  } else {
    document.body.style.overflow = '';
  }
};

// 关闭侧边栏
const closeSidebar = () => {
  sidebarOpen.value = false;
  document.body.style.overflow = '';
};

// 导航到指定路径
const navigateTo = (path) => {
  router.push(path);
};

// 导航到设置页面
const navigateToSettings = (event) => {
  if (event) {
    event.stopPropagation();
  }
  closeDropdown();
  router.push('/settings');
};

// 生命周期钩子
onMounted(() => {
  // 预加载图片，减少闪烁
  preloadImages();
  
  checkScreenSize();
  window.addEventListener('resize', checkScreenSize);
  document.addEventListener('click', handleClickOutside);
});

onUnmounted(() => {
  window.removeEventListener('resize', checkScreenSize);
  document.removeEventListener('click', handleClickOutside);
});
</script>

<style scoped>
@import './top.css';
</style>